.container {
  width: 100vw;
  height: 100vh;
  font-family: 'Noto Sans SC', 'Roboto', sans-serif;
  font-size: 18px;
  display: flex;
  align-items: stretch;
  justify-content: space-between;
}

.left {
  width: calc(50vw - 1px);
  border-right: 1px solid #eee;
}

.left textarea {
  margin: 0;
  padding: 8px;
  width: calc(100% - 16px);
  height: calc(100% - 16px - 10px);
  font-family: 'Noto Sans SC', 'Roboto', sans-serif;
  font-size: 18px;
  border: none;
  resize: none;
  outline: none;
}

.right {
  width: 50vw;
  overflow: auto;
}

.right > div {
  width: 95%;
  margin: 0 auto 24px;
  padding: 8px;
  /*white-space: pre-wrap;*/
}

::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  background: rgba(0, 0, 0, 0.05);
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.15);
  border-radius: 8px;
}
